.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: rgb(3, 29, 154);;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.background-image {
  background-image: url('./images/eys.jpg');
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1; 
}

.content {
  font-family: Helvetica;
  /*font-family: 'Times New Roman', Times, serif; */
  margin: 10vh auto; 
  padding: 20px; 
  background-color: rgba(255, 255, 255, 0.9); 
  border-radius: 0px; 
  width: 90%; 
  max-width: 1000px;
}

.content h1 {
  font-family: 'Times New Roman', Times, serif;
  color: rgb(3, 29, 154); /* Text color for the heading */
  text-shadow: 1px 1px 1px rgba(118, 77, 1, 1.0); /* Add a text shadow with a different color */
  font-weight: normal;
  text-align: center;
}


.container {
  display: flex;
  justify-content: space-around; 
  width: 100%;
  padding: 20px; 
  box-sizing: border-box;
}

.box {
  width: 50%; 
  height: 100px;
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 10px; 
  padding: 10px; 
  box-sizing: border-box; 
}

.box.collapsed {
  /*background-image: url('./images/quantum_computer.webp');*/
  background-size: cover;
  background-position: center;
}

.box.expanded {
  height: auto;
}

.menu {
  list-style: none;
  padding: 0;
  font-size: calc(3px + 2vmin);
}

.menu li {
  display: inline;
  margin: 0 10px;
}

.menu a {
  color: rgb(3, 29, 154);;
  text-decoration: none;
}

.menu a:hover {
  color: rgba(118, 77, 1, 1.0);
  text-decoration: none;
}

.meList {
  list-style: none;
  padding: 0;
  text-align: left;
  margin: 0 auto; 
  width: fit-content; 
}

.meList li:before {
  content: "â˜…";
  margin-right: 5px;
  color: rgb(3, 29, 154);
}

.content h2 {
  justify-content: top;
  margin-top: 25px;
  font-size: calc(3px + 2vmin);
  font-weight: 500;
}

.content p {
  text-align: left;
  margin:5%;
}

.content li {
  text-align: left;
}
